import React, { useState } from "react";
import "./App.css";
import Title from './components/Title';


function Todo({ todo, index, completeTodo, removeTodo }) {
  return (
    <div
      className="todo"
      style={{ textDecoration: todo.isCompleted ? "line-through" : " " }}
    >
      {todo.text}

      <div className = "space">
        <button className = "complete" onClick={() => completeTodo(index)}>complete</button>
        <button className = "remove" onClick={() => removeTodo(index)}>x</button>
      </div>
    </div>
  );
}

function TodoForm({ addTodo }) {
  const [value, setValue] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    addTodo(value);
    setValue("");
  };

  return (
    <form onSubmit={handleSubmit}>
      <input placeholder = 'Enter A New Task'
        type="text"
        className="input"
        value={value}
        onChange={e => setValue(e.target.value)}
      />
    </form>
  );
}

function App() {
  const [todos, setTodos] = useState([
    {
      text: "Go to class",
      isCompleted: false
    },
    {
      text: "Meet friend for lunch",
      isCompleted: false
    },
    {
      text: "Go to the gym and workout man",
      isCompleted: false
    }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  };

  const completeTodo = index => {
    const newTodos = [...todos];
    newTodos[index].isCompleted = true;
    setTodos(newTodos);
  };

  const removeTodo = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  };



  return (
   <div> <Title/>
      <div className="app">
        <div className="todo-list">
         {todos.map((todo, index) => (
           <Todo
              key={index}
              index={index}
              todo={todo}
              completeTodo={completeTodo}
              removeTodo={removeTodo}
             />
            ))}
            
          <TodoForm addTodo={addTodo}>
      
          </TodoForm>
       </div>
     </div>
     <div className = 'task'>
       { (todos.length>0)? "Total Tasks: " + todos.length: "No Tasks"}</div>
      <footer>
       Â© 2020 Darren Wu 
      </footer>
   </div>
  );
}

export default App;